footer{background-color:#FAF8FF;margin-top:-10px}footer .wrp{padding:30px 5px}footer .wrp .col_4{width:100%;padding:2px 10px}@media only screen and (min-width: 768px){footer .wrp .col_4{width:49%}}@media only screen and (min-width: 992px){footer .wrp .col_4{width:24%}}footer .wrp .col_4 h4{font-weight:600;text-transform:capitalize;position:relative;font-size:19px}footer .wrp .col_4 h4:after{position:absolute;left:0px;width:70px;height:2px;background-image:linear-gradient(45deg, #6639a6 0%, #3490de 100%);content:"";border-radius:3px;transform:translateY(35px)}footer .wrp .col_4 p{font-size:15px}footer .wrp .col_4 ul{padding:0px}footer .wrp .col_4 ul li{list-style:none;display:block;padding:5px 0px}footer .wrp .col_4 ul li a{text-decoration:none;font-size:15px;transition:.3s}footer .wrp .col_4 ul li a:hover{color:#f0a500}footer .wrp .col_4 a img{width:65%}footer .wrp .col_4 address{font-style:normal}footer .wrp .col_4 address a{text-decoration:none;font-size:15px;transition:.3s}footer .wrp .col_4 address a p{font-size:15px}footer .wrp .col_4 address a p:hover{color:#f0a500}@media only screen and (min-width: 992px){footer .wrp .col_4:nth-child(2){padding-left:20px}}footer .pre_footer{padding:2px 3%;background-color:#F4F4F4;border-top:1px solid #e8e8e8}footer .pre_footer h5{text-transform:capitalize;letter-spacing:1px}footer .pre_footer h5 a{text-decoration:none;font-size:15px;transition:.3s;color:black;font-weight:300}footer .pre_footer h5 a:hover{color:#f0a500}
